/*TABLET*/
@media only screen and (min-width:769px) and (max-width:900px) {

    /*INDEX*/
    .body__recien-llegados--titulo {
        width: 28%;
        margin-left: 6%;
    }

    .body__recien-llegados--productos {
        grid-template-columns: repeat(4, 1fr);
        column-gap: 8%;
    }

    #body__recien-llegados--producto {
        grid-column: 2;
    }

    #body__recien-llegados--producto2 {
        grid-column: 3;
    }

    .body__pie-de-pagina {
        column-gap: 5%;
        overflow: visible;
    }

    .body__pie-de-pagina--cuadrado {
        line-height: 2;
        text-align: center;
        width: 24%;
        padding: 3.5% 0px;
    }

    /*PRODUCTOS*/

    .body__fila {
        margin-left: 20vw;
        grid-template-columns: repeat(3, 25%);
    }

    .body__producto1 {
        grid-column: 1;
    }

    #footer__productos {
        margin-top: 15%;
    }

    /*CONTACTO*/
    .body__h4 {
        padding-top: 5vw;
    }

    /*FOOTER*/
    .footer__seguinos--seguinos-en {
        font-size: 2.5vw;
    }

    .footer__ul {
        font-size: 1.75vw;
    }
}

/*TABLET*/
@media only screen and (min-width:481px) and (max-width:768px) {

    /*INDEX*/
    .header__menu {
        column-gap: 1%;
    }

    .header__menu--select {
        font-size: 20px;
    }

    .body__recien-llegados--titulo {
        width: 28%;
        margin-left: 1%
    }

    .body__recien-llegados--productos {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 0.25%;
        overflow: hidden;
    }

    #body__recien-llegados--producto {
        grid-column: 1;
    }

    .body__pie-de-pagina--cuadrado {
        line-height: 2;
        text-align: center;
        width: 30%;
        padding: 3.5% 0px;
    }

    /*HEADER GENÉRICO*/

    #header__header--title2 {
        font-size: 200%;
    }

    .header__header--title-logo {
        justify-content: space-around;
        column-gap: 0;
    }

    /*PRODUCTOS*/

    .body__ordenar-por {
        padding-bottom: 5vw;
    }

    .body__fila {
        margin-left: 18vw;
        grid-template-columns: repeat(2, 45%);
        padding-top: 0px;
    }

    #body__producto1 {
        grid-column: 1;
    }

    .body__h2--productos {
        padding-left: 12%;
    }

    .body__ordenar-por {
        padding-top: 0.7%;
        padding-left: 12%;
    }

    /*CONTACTO*/
    .body__h4 {
        padding-top: 5vw;
    }

    .body__seccion--seguinos {
        display: flex;
        justify-content: space-between;
        column-gap: 20%;
        padding-top: 4%;
        padding-left: 10vw;
        padding-right: 10vw;
    }

    #btn__productos {
        margin-top: 8vw;
    }

    #footer__productos {
        margin-top: 25%;
    }

    /*NOSOTROS*/

    #body__image--nosotros {
        padding: 3% 0px;
    }

    #footer__nosotros {
        margin-top: 10%;
    }

    /*FOOTER*/
    .footer__seguinos--seguinos-en {
        font-size: 2.5vw;
    }

    .footer__ul {
        font-size: 1.75vw;
    }
}

/*TABLET*/
@media only screen and (min-width:320px) and (max-width:480px) {

    /*INDEX*/
    .header__header--title {
        font-size: 120%;
    }

    .header__menu {
        display: flex;
        flex-direction: row;
        justify-content: center;
        column-gap: 0.3%;
    }

    .header__menu--select {
        font-size: 70%;
    }

    .body__parrafo {
        font-size: 118%;
    }

    .body__recien-llegados--titulo {
        width: 60%;
        height: 40px;
        margin-left: 2%;
        font-size: 100%;
    }

    .body__recien-llegados--productos {
        display: block;
    }

    #body__recien-llegados--producto {
        padding-top: 6%;
    }

    #body__recien-llegados--producto2 {
        padding-top: 10%;
    }

    .body__pie-de-pagina {
        font-size: 90%;
        display: block;
        margin: 0px;
    }

    .body__pie-de-pagina--cuadrado {
        margin: auto;
    }

    .body__pie-de-pagina--cuadrado-texto {
        font-size: 6vw;
    }

    /*HEADER GENÉRICO*/

    #header__header--title2 {
        font-size: 120%;
    }

    .header__header--title-logo {
        justify-content: space-around;
        column-gap: 0;
    }

    /*PRODUCTOS*/

    .body__fila {
        display: block;
        padding-top: 0px;
        margin-left: 15vw;
    }

    .body__ordenar-por {
        padding-top: 0.7%;
        padding-left: 12%;
    }

    .body__h2--productos {
        padding-left: 12%;
    }

    .body__producto {
        padding-top: 40%;
        height: 450px;
    }

    .body__producto1 {
        padding-top: 20%;
        height: 450px;
    }

    .btn__agregar-al-carrito {
        width: 90%;
        height: 45px;
    }

    #footer__productos {
        margin-top: 25%;
    }

    #btn__productos {
        margin-top: 25vw;
    }

    /*NOSOTROS*/

    #body__image--nosotros {
        padding: 3% 0px;
    }

    #footer__nosotros {
        margin-top: 10%;
    }

    /*CONTACTO*/
    .body__h4 {
        padding-top: 5vw;
    }

    .body__seccion--seguinos {
        column-gap: 20%;

    }

    iframe {
        width: auto;
    }

    legend {
        display: none;
    }

    .contacto__formulario {
        display: inline;
    }

    /*FOOTER*/
    .footer {
        padding: 3% 5%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .footer__seguinos--seguinos-en {
        font-size: 3.5vw;
    }

    .footer__ul {
        flex-wrap: nowrap;
        font-size: 3vw;
    }

    .footer__ul--preguntas-frecuentes {
        border-right: 1px solid transparent;
    }
}